import React from 'react'
import axios from 'axios';
import ToastNotif from './ToastNotif';

class CreateItemModal extends React.Component {

    state = {
        name: "",
        price: 0,
        hasError: false,
        errorMessage: ''
    }

    updateName = (e) => {
        this.setState({ name: e.target.value });
    }

    updatePrice = (e) => {
        this.setState({ price: e.target.value });
    }

    create = () => {
        if (this.props.mode === 'update') {
            axios.patch(`items`, {
                id: this.props.item.id,
                name: this.state.name,
                price: this.state.price
            }).then(res => {
                this.props.onCreate();
            }).catch(error => {
                this.setState({
                    hasError: true,
                    errorMessage: 'Failed to update item.'
                }, function () {
                    setTimeout(() => {
                        this.setState({ hasError: false });
                    }, 2000)
                });
            });
        } else {
            axios.post(`items`, {
                name: this.state.name,
                price: this.state.price
            }).then(res => {
                this.props.onCreate();
            }).catch(error => {
                this.setState({
                    hasError: true,
                    errorMessage: 'Failed to create item.'
                }, function () {
                    setTimeout(() => {
                        this.setState({ hasError: false });
                    }, 2000)
                });
            });
        }
    }

    componentDidMount() {
        if (this.props.item) {
            this.setState({ 
                name: this.props.item.name,
                price: this.props.item.price
            });

        }
    }

    render() {
        return <>
            <ToastNotif className={this.state.hasError ? undefined : 'hidden'} message={this.state.errorMessage} />


            <div className="fixed z-10 inset-0 overflow-y-auto" aria-labelledby="modal-title" role="dialog" aria-modal="true">
                <div className="flex items-end justify-center min-h-screen pt-4 px-4 pb-20 text-center sm:block sm:p-0">
                    <div className="fixed inset-0 bg-gray-500 bg-opacity-75 transition-opacity" onClick={this.props.onCancel} aria-hidden="true"></div>
                    <span className="hidden sm:inline-block sm:align-middle sm:h-screen" aria-hidden="true">&#8203;</span>
                    <div className="inline-block align-bottom bg-white rounded-lg text-left overflow-hidden shadow-xl transform transition-all sm:my-8 sm:align-middle sm:max-w-lg h-auto sm:w-full">
                        <div className="bg-white px-4 pt-5 pb-4 sm:p-6 sm:pb-4">
                            <div className="sm:flex sm:items-start">
                                <div className="mt-3 sm:mt-0 sm:ml-4 text-left w-full">

                                    <div className="p-2">
                                        <label htmlFor="item_name" className="block text-sm font-medium text-gray-700">Name</label>
                                        <input type="text" id="item_name" className="p-3 mt-1 focus:ring-yellow-900 focus:border-yellow-500 block w-full shadow-sm sm:text-sm border-gray-300 border-2 rounded-md" defaultValue={this.props.item ? this.props.item.name : ""} onChange={this.updateName} />
                                    </div>

                                    <div className="p-2">
                                        <label htmlFor="item_price" className="block text-sm font-medium text-gray-700">Price</label>
                                        <input type="number" id="item_price" className="p-3 mt-1 focus:ring-yellow-900 focus:border-yellow-500 block w-full shadow-sm sm:text-sm border-gray-300 border-2 rounded-md" defaultValue={this.props.item ? this.props.item.price : ""} onChange={this.updatePrice} />
                                    </div>

                                </div>
                            </div>
                        </div>
                        <div className="bg-gray-50 px-4 py-3 sm:px-6 sm:flex sm:flex-row-reverse grid grid-cols-2 gap-1">
                            <button type="button" className="w-full inline-flex justify-center rounded-md border border-transparent shadow-sm px-4 py-2 bg-yellow-900 text-base font-medium text-white hover:bg-yellow-900 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-yellow-900 sm:ml-3 sm:w-auto sm:text-sm" onClick={this.create}>
                                {this.props.mode === 'create' ? 'Create' : 'Update'}
                            </button>
                            <button type="button" className="w-full inline-flex justify-center rounded-md border border-gray-300 shadow-sm px-4 py-2 bg-white text-base font-medium text-gray-700 hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500 sm:mt-0 sm:ml-3 sm:w-auto sm:text-sm" onClick={this.props.onCancel}>
                                Cancel
              </button>
                        </div>
                    </div>
                </div>
            </div>
        </>;
    }
}

export default CreateItemModal;
