import React from 'react'
import axios from 'axios';
import OrderListItem from './OrderListItem';
import CreateOrderModal from './CreateOrderModal';
import OrderDetailsModal from './OrderDetailsModal';
import ToastNotif from './ToastNotif';

class OrderList extends React.Component {
    state = {
        order: null,
        orders: [],
        items: [],
        showCreateOrderModal: false,
        showOrderDetailsModal: false,
        hasError: false,
        errorMessage: ''
    }

    getPendingOrders = () => {
        axios.get(`orders?status=pending&datefrom=now`)
            .then(res => {
                const orders = res.data;
                this.setState({ orders });
            }).catch(error => {
                this.setState({
                    hasError: true,
                    errorMessage: 'Failed to get orders.'
                }, function () {
                    setTimeout(() => {
                        this.setState({ hasError: false });
                    }, 2000)
                });
            });
    }

    completeOrder = () => {
        axios.patch(`orders`, { id: this.state.order.id, status: 'completed' })
            .then(res => {
                this.hideOrderDetailsModal();
                this.getPendingOrders();
            }).catch(error => {
                this.setState({
                    hasError: true,
                    errorMessage: 'Failed to update order.'
                }, function () {
                    setTimeout(() => {
                        this.setState({ hasError: false });
                    }, 2000)
                });
            });
    }

    createOrder = () => {
        this.setState({order:null}, () => {
            this.showCreateOrderModal();
        });
    }

    editOrder = (order, e) => {
        this.hideOrderDetailsModal();

        var i;
        let items_in_qty = [];
        for (i = 0; i < order.items.length; i++) {
            items_in_qty[order.items[i].item_id] = order.items[i].quantity;
        }

        order.items_in_qty = items_in_qty;


        this.setState({ order: order }, () => {
            console.log(order);

            this.showCreateOrderModal();
        });
    }

    showCreateOrderModal = () => {
        this.setState({ showCreateOrderModal: true });
    }

    hideCreateOrderModal = () => {
        this.setState({ showCreateOrderModal: false });
    }

    showOrderDetailsModal = (order, e) => {
        this.setState({ order: order }, function () {
            this.setState({ showOrderDetailsModal: true });
        });
    }

    hideOrderDetailsModal = () => {
        this.setState({ showOrderDetailsModal: false });
    }

    handleOrderCreated = () => {
        this.getPendingOrders()
        this.hideCreateOrderModal()
    }

    handleOrderUpdated = () => {
        this.getPendingOrders();
        this.hideCreateOrderModal();
    }

    handleOrderCompleted = () => {
        this.getPendingOrders()
    }

    componentDidMount() {
        axios.get(`items`)
            .then(res => {
                const items = res.data;
                this.setState({ items });
            }).catch(error => {
                this.setState({
                    hasError: true,
                    errorMessage: 'Failed to get items.'
                }, function () {
                    setTimeout(() => {
                        this.setState({ hasError: false });
                    }, 2000)
                });
            });
        this.getPendingOrders()
    }

    render() {
        return <>
            <ToastNotif className={this.state.hasError ? undefined : 'hidden'} message={this.state.errorMessage} />

            {this.state.showCreateOrderModal === true &&
                <CreateOrderModal
                    items={this.state.items}
                    order={this.state.order}
                    onCreate={this.handleOrderCreated}
                    onUpdate={this.handleOrderUpdated}
                    onClose={this.hideCreateOrderModal}
                />
            }

            {this.state.showOrderDetailsModal === true &&
                <OrderDetailsModal
                    order={this.state.order}
                    onEdit={(e) => this.editOrder(this.state.order, e)}
                    onComplete={this.completeOrder}
                    onClose={this.hideOrderDetailsModal}
                />
            }

            <button onClick={this.createOrder} className="bottom-3 right-3 fixed inline-flex items-center justify-center px-5 py-3 border border-transparent text-base font-medium rounded-full text-white bg-yellow-900 hover:bg-yellow-800 outline-none focus:outline-none">
                +
            </button>

            <div className="flex flex-col">
                <div className="-my-2 overflow-y-auto sm:-mx-6 lg:-mx-8">
                    <div className="py-2 align-middle inline-block min-w-full sm:px-6 lg:px-8">
                        <div className="shadow overflow-hidden border-b border-gray-200 sm:rounded-lg">
                            <table className="min-w-full divide-y divide-gray-200">
                                <tbody className="bg-white divide-y divide-gray-200">
                                    {this.state.orders.map((order, index) => {
                                        return <OrderListItem
                                            key={order.id}
                                            order={order}
                                            onClick={(e) => this.showOrderDetailsModal(order, e)}
                                        />
                                    })}
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
            </div>
        </>;
    }
}

export default OrderList;
