import React from 'react'
import formatMoney from '../Helpers';

class OrderDetailsModal extends React.Component {
  render() {
    return <>
      <div className="fixed z-10 inset-0 overflow-y-auto" aria-labelledby="modal-title" role="dialog" aria-modal="true">
        <div className="flex items-end justify-center min-h-screen pt-4 px-4 pb-20 text-center sm:block sm:p-0">
          <div className="fixed inset-0 bg-gray-500 bg-opacity-75 transition-opacity" onClick={this.props.onClose} aria-hidden="true"></div>
          <span className="hidden sm:inline-block sm:align-middle sm:h-screen" aria-hidden="true">&#8203;</span>
          <div className="inline-block align-bottom bg-white rounded-lg text-left overflow-hidden shadow-xl transform transition-all sm:my-8 sm:align-middle sm:max-w-lg h-auto sm:w-full">
            <div className="bg-white px-4 pt-5 pb-4 sm:p-6 sm:pb-4">
              <div className="sm:flex sm:items-start">
                <div className="mt-3 text-center sm:mt-0 sm:ml-4 sm:text-left w-full">
                  <h3 className="text-lg leading-6 font-medium text-gray-900 text-center" id="modal-title">
                    Order {this.props.order.claim_number}
                  </h3>
                  <div className="-my-2 overflow-x-auto sm:-mx-6 lg:-mx-8">
                    <div className="py-2 align-middle inline-block min-w-full sm:px-6 lg:px-8">
                      <div className="shadow overflow-hidden border-b border-gray-200 sm:rounded-lg">
                        <table className="min-w-full divide-y divide-gray-200">
                          <thead className="bg-gray-50">
                            <tr>
                              <th scope="col" className="px-1 lg:px-4 py-4 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                                Item
                              </th>
                              <th scope="col" className="px-1 lg:px-4 py-4 text-left text-xs font-medium text-gray-500 uppercase tracking-wider text-right">
                                Subtotal
                              </th>
                            </tr>
                          </thead>
                          <tbody className="bg-white divide-y divide-gray-200">
                            {this.props.order.items.map((item, index) => {
                              return <tr key={item.item.id}>
                                <td className="px-1 lg:px-4 py-4 whitespace-nowrap text-xs text-gray-500 text-left">
                                  {item.quantity} {item.item.name}
                                </td>
                                <td className="px-1 lg:px-4 py-4 whitespace-nowrap text-xs text-gray-500 text-right">
                                  {formatMoney(item.quantity * item.item.price)}
                                </td>
                              </tr>
                            })}
                          </tbody>
                        </table>
                      </div>
                      <div className="px-1 lg:px-4 py-2 text-right">
                        Total: {formatMoney(parseFloat(this.props.order.total))}
                      </div>

                      <span className="hidden lg:contents">
                        <div className="mt-3 sm:mt-0 text-left w-full grid grid-cols-1 text-sm">
                            <div className="col-span-1 p-2">{this.props.order.name && 'Name: ' + this.props.order.name }</div>
                            <div className="col-span-1 p-2">{this.props.order.fulfillment && 'Fulfillment: ' + this.props.order.fulfillment }</div>
                            <div className="col-span-1 p-2">{this.props.order.notes && 'Notes: ' + this.props.order.notes }</div>
                        </div>
                      </span>


                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div className="bg-gray-50 px-4 py-3 sm:px-6 sm:flex sm:flex-row-reverse grid grid-cols-2 gap-1">
              
              <button type="button" className="w-full inline-flex justify-center rounded-md border border-gray-300 shadow-sm px-4 py-2 bg-white text-base font-medium text-gray-700 hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500 sm:mt-0 sm:ml-3 sm:w-auto sm:text-sm" onClick={this.props.onClose}>
                Close
              </button>
              {
                this.props.order.status === 'pending' && <>
                <button type="button" className="w-full inline-flex justify-center rounded-md border border-gray-300 shadow-sm px-4 py-2 bg-white text-base font-medium text-gray-700 hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500 sm:mt-0 sm:ml-3 sm:w-auto sm:text-sm" onClick={this.props.onEdit}>
                  Edit
                </button>
                <button type="button" className="w-full inline-flex justify-center rounded-md border border-transparent shadow-sm px-4 py-2 bg-yellow-900 text-base font-medium text-white hover:bg-yellow-800 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-yellow-900 sm:ml-3 sm:w-auto sm:text-sm" onClick={this.props.onComplete}>
                  Complete
                </button>
                </>
              }
              
              
            </div>
          </div>
        </div>
      </div>
    </>;
  }
}

export default OrderDetailsModal;
