import React from 'react'
import Moment from 'react-moment';
import icon from '../icon.jpg';

class OrderListItem extends React.Component {
    render() {
        return <>
            <tr className="cursor-pointer hover:bg-gray-100 hover:shadow-lg ease-in-out duration-200" onClick={this.props.onClick}>
                <td className="px-6 py-4 whitespace-nowrap">
                    <div className="flex items-center">
                        <div className="flex-shrink-0 h-10 w-10">
                            <img className="h-10 w-10 rounded-full" src={icon} alt="" />
                        </div>
                        <div className="ml-4">
                            <div className="text-sm font-medium text-gray-900 text-left">
                                Order {this.props.order.claim_number} {this.props.order.name && ' - ' + this.props.order.name}
                            </div>
                            <div className="text-sm text-gray-500 text-left">
                                <Moment fromNow>{this.props.order.created_at}</Moment>
                            </div>
                        </div>
                    </div>
                </td>
            </tr>
        </>;
    }
}

export default OrderListItem;
