import React from 'react';
import axios from 'axios';
import OrderListItem from './OrderListItem';
import OrderDetailsModal from './OrderDetailsModal';
import ToastNotif from './ToastNotif';

class CompletedOrderList extends React.Component {
    state = {
        order: null,
        orders: [],
        showOrderDetailsModal: false,
        hasError: false,
        errorMessage: ''
    }

    getCompletedOrders = () => {
        axios.get(`orders?status=completed&datefrom=now`)
            .then(res => {
                const orders = res.data;
                this.setState({ orders });
            }).catch(error => {
                this.setState({
                    hasError: true,
                    errorMessage: 'Failed to get orders.'
                }, function () {
                    setTimeout(() => {
                        this.setState({ hasError: false });
                    }, 2000)
                });
            });
    }

    showOrderDetailsModal = (order, e) => {
        this.setState({ order: order }, function () {
            this.setState({ showOrderDetailsModal: true });
        });
    }

    hideOrderDetailsModal = () => {
        this.setState({ showOrderDetailsModal: false });
    }

    componentDidMount() {
        this.getCompletedOrders()
    }

    render() {
        return <>
            <ToastNotif className={this.state.hasError ? undefined : 'hidden'} message={this.state.errorMessage} />

            {this.state.showOrderDetailsModal === true &&
                <OrderDetailsModal
                    order={this.state.order}
                    onClose={this.hideOrderDetailsModal}
                />
            }

            <div className={`flex flex-col ${this.props.className}`}>
                <div className="-my-2 overflow-y-auto sm:-mx-6 lg:-mx-8">
                    <div className="py-2 align-middle inline-block min-w-full sm:px-6 lg:px-8">
                        <div className="shadow overflow-hidden border-b border-gray-200 sm:rounded-lg">
                            <table className="min-w-full divide-y divide-gray-200">
                                <tbody className="bg-white divide-y divide-gray-200">
                                    {this.state.orders.map((order, index) => {
                                        return <OrderListItem
                                            key={order.id}
                                            order={order}
                                            onClick={(e) => this.showOrderDetailsModal(order, e)}
                                        />
                                    })}
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
            </div>
        </>;
    }
}

export default CompletedOrderList;
