import React from 'react'
import axios from 'axios';
import formatMoney from '../Helpers';
import ToastNotif from './ToastNotif';

class CreateOrderModal extends React.Component {

  state = {
    items: this.props.items,
    total: 0,
    order_name: '',
    order_fulfillment: 'Dine in',
    order_comment: '',
    hasError: false,
    errorMessage: '',
    isCreating: false
  }

  reset = () => {
    let items = this.state.items;

    let total = 0;

    for (var i = 0; i < items.length; i++) {
      items[i].quantity = (this.props.order && this.props.order.items_in_qty[items[i].id] !== undefined) ? this.props.order.items_in_qty[items[i].id] : 0;

      total += (items[i].quantity * items[i].price);
    }

    if (this.props.order) {
      if (this.props.order.name) {
        this.setState({order_name: this.props.order.name});
      } else {
        this.setState({order_name: ''});
      }
  
      if (this.props.order.fulfillment) {
        this.setState({order_fulfillment: this.props.order.fulfillment});
      } else {
        this.setState({order_fulfillment: 'Dine in'});
      }
  
      if (this.props.order.notes) {
        this.setState({order_comment: this.props.order.notes});
      } else {
        this.setState({order_comment: ''});
      }
    }


    this.setState({
      total: total,
      items: items,
      isCreating: false
    });
  }

  cancel = () => {
    this.reset();
    this.props.onClose();
  }

  create = () => {
    let items = [];
    for (var i = 0; i < this.state.items.length; i++) {
      if (this.state.items[i].quantity) {
        items.push({
          id: this.state.items[i].id,
          quantity: this.state.items[i].quantity
        })
      }
    }

    if (items.length && !this.state.isCreating) {
      this.setState({ isCreating: true }, () => {
        axios.post(`orders`, {
          items: items,
          name: this.state.order_name,
          fulfillment: this.state.order_fulfillment,
          notes: this.state.order_comment
        }).then(res => {
          this.reset();
          this.props.onCreate();
        }).catch(error => {
          this.setState({
            hasError: true,
            errorMessage: 'Failed to create order.',
            isCreating: false
          }, function () {
            setTimeout(() => {
              this.setState({ hasError: false });
            }, 2000)
          });
        });
      });
    }
  }

  updateName = (e) => {
    this.setState({order_name: e.target.value});
  }

  updateFulfillment = (e) => {
    this.setState({order_fulfillment: e.target.value});
  }

  updateComment = (e) => {
    this.setState({order_comment: e.target.value});
  }

  componentDidMount() {
    this.reset();
  }

  update = () => {
    let items = [];
    for (var i = 0; i < this.state.items.length; i++) {
      if (this.state.items[i].quantity) {
        items.push({
          id: this.state.items[i].id,
          quantity: this.state.items[i].quantity
        })
      }
    }

    if (items.length && !this.state.isCreating) {
      this.setState({ isCreating: true }, () => {
        axios.post(`orders`, {
          items: items,
          order_id: this.props.order.id,
          name: this.state.order_name,
          fulfillment: this.state.order_fulfillment,
          notes: this.state.order_comment
        }).then(res => {
          this.reset();
          this.props.onUpdate();
        }).catch(error => {
          this.setState({
            hasError: true,
            errorMessage: 'Failed to update order.',
            isCreating: false
          }, function () {
            setTimeout(() => {
              this.setState({ hasError: false });
            }, 2000)
          });
        });
      });
    }
  }

  addItem = (item, e) => {
    this.setState({ total: this.state.total + parseFloat(item.price) });
    let items = this.state.items;
    for (var i = 0; i < items.length; i++) {
      if (item.id === items[i].id) {
        if (!items[i].quantity) {
          items[i].quantity = 0;
        }
        items[i].quantity++;
      }
    }
  }

  removeItem = (item, e) => {

    if (!item.quantity) {
      return;
    }

    this.setState({ total: this.state.total - parseFloat(item.price) });
    let items = this.state.items;
    for (var i = 0; i < items.length; i++) {
      if (item.id === items[i].id) {
        if (!items[i].quantity) {
          items[i].quantity = 0;
        }
        items[i].quantity--;
      }
    }
  }

  render() {
    return <>
      <ToastNotif className={this.state.hasError ? undefined : 'hidden'} message={this.state.errorMessage} />

      <div className="fixed z-10 inset-0 overflow-y-auto" aria-labelledby="modal-title" role="dialog" aria-modal="true">
        <div className="flex items-end justify-center min-h-screen pt-4 px-4 pb-20 text-center sm:block sm:p-0">
          <div className="fixed inset-0 bg-gray-500 bg-opacity-75 transition-opacity" onClick={this.cancel} aria-hidden="true"></div>
          <span className="hidden sm:inline-block sm:align-middle sm:h-screen" aria-hidden="true">&#8203;</span>
          <div className="inline-block align-bottom bg-white rounded-lg text-left overflow-hidden shadow-xl transform transition-all sm:my-8 sm:align-middle sm:max-w-lg h-auto sm:w-full">
            <div className="bg-white px-4 pt-5 pb-4 sm:p-6 sm:pb-4">
              <div className="sm:flex sm:items-start">
                <div className="mt-3 text-center sm:mt-0 ml-2 sm:text-left w-full">
                  <ul className="max-h-72 overflow-y-auto">
                    {this.state.items.map((item, index) => {
                      return <li key={item.id}>
                        <div className="flex flex-nowrap grid grid-cols-3 lg:grid-cols-4 gap-1">
                          <div className="col-span-2 lg:col-span-3 pt-4 text-left text-xs">
                            {item.name}
                          </div>
                          <div className="col-span-1 grid grid-cols-3 gap-0">
                            <div className="flex justify-center">
                              <button onClick={(e) => this.addItem(item, e)} className="mt-2 px-2 inline-flex items-center justify-center px-1 py-1 border border-transparent text-base font-medium rounded-md text-white bg-yellow-900 hover:bg-yellow-800">+</button>
                            </div>
                            <div className="text-center pt-3">
                              {item.quantity || 0}
                            </div>
                            <div className="flex justify-center">
                              <button onClick={(e) => this.removeItem(item, e)} className="mt-2 px-2 inline-flex items-center justify-center px-1 py-1 border border-transparent text-base font-medium rounded-md text-white bg-yellow-900 hover:bg-yellow-800">-</button>
                            </div>
                          </div>
                        </div>
                      </li>
                    })}
                  </ul>
                  <br />
                  <div className="w-full text-center mb-6">
                    Total: {formatMoney(parseFloat(this.state.total))}
                  </div>
                  <span className="hidden lg:contents">
                    <div className="mt-3 sm:mt-0 text-left w-full grid grid-cols-2">

                      <div className="p-2">
                        <label htmlFor="order_name" className="block text-sm font-medium text-gray-700">Order Name</label>
                        <input type="text" id="order_name" className="p-3 mt-1 focus:ring-yellow-900 focus:border-yellow-500 block w-full shadow-sm sm:text-sm border-gray-300 border-2 rounded-md" defaultValue={this.state.order_name ? this.state.order_name : ""} onChange={this.updateName} />
                      </div>

                      <div className="p-2">
                        <label htmlFor="order_fulfillment" className="block text-sm font-medium text-gray-700">Fulfillment</label>
                        <select id="order_fulfillment" className="p-3 mt-1 focus:ring-yellow-900 focus:border-yellow-500 block w-full shadow-sm sm:text-sm border-gray-300 border-2 rounded-md" defaultValue={this.state.order_fulfillment ? this.state.order_fulfillment : ""} onChange={this.updateFulfillment}>
                          <option>Dine in</option>
                          <option>Take out</option>
                        </select>
                      </div>

                      <div className="col-span-2 p-2">
                        <label htmlFor="order_comments" className="block text-sm font-medium text-gray-700">Additional Notes</label>
                        <input type="text" id="order_comments" className="p-3 mt-1 focus:ring-yellow-900 focus:border-yellow-500 block w-full shadow-sm sm:text-sm border-gray-300 border-2 rounded-md" defaultValue={this.state.order_comment ? this.state.order_comment : ""} onChange={this.updateComment} />
                      </div>

                    </div>


                  </span>


                </div>
              </div>
            </div>
            <div className="bg-gray-50 px-4 py-3 sm:px-6 sm:flex sm:flex-row-reverse grid grid-cols-2 gap-1">
              <button type="button" className="w-full inline-flex justify-center rounded-md border border-transparent shadow-sm px-4 py-2 bg-yellow-900 text-base font-medium text-white hover:bg-yellow-900 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-yellow-900 sm:ml-3 sm:w-auto sm:text-sm" onClick={this.props.order ? this.update : this.create}>
                {this.props.order ? 'Update' : 'Create'}
              </button>
              <button type="button" className="w-full inline-flex justify-center rounded-md border border-gray-300 shadow-sm px-4 py-2 bg-white text-base font-medium text-gray-700 hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500 sm:mt-0 sm:ml-3 sm:w-auto sm:text-sm" onClick={this.cancel}>
                Cancel
              </button>
            </div>
          </div>
        </div>
      </div>
    </>;
  }
}

export default CreateOrderModal;
