import React from 'react';
import axios from 'axios';
import CreateItemModal from './CreateItemModal';
import { ArrowUp, ArrowDown, Pencil } from "heroicons-react";
import ToastNotif from './ToastNotif';

class MenuItemList extends React.Component {

    state = {
        item: null,
        items: [],
        createMode: 'create',
        showCreateItemModal: false,
        hasError: false,
        errorMessage: ''
    };

    getItems = () => {
        axios.get(`items`)
            .then(res => {
                const items = res.data;
                this.setState({ items });
            }).catch(error => {
                this.setState({
                    hasError: true,
                    errorMessage: 'Failed to get items.'
                }, function () {
                    setTimeout(() => {
                        this.setState({ hasError: false });
                    }, 2000)
                });
            });
    }

    showCreateItemModal = (mode, item, e) => {
        this.setState({createMode: mode, item: item}, function () {
            this.setState({showCreateItemModal: true});
        });
    }

    handleItemCreated = () => {
        this.getItems();
        this.hideCreateItemModal();
    }

    hideCreateItemModal = () => {
        this.setState({showCreateItemModal: false});
    }

    moveItemUp = (item_id, e) => {
        axios.patch(`items/move-up`, {
            id: item_id,
        }).then(res => {
            this.getItems();
        }).catch(error => {
            this.setState({
                hasError: true,
                errorMessage: 'Failed to move item.'
            }, function () {
                setTimeout(() => {
                    this.setState({ hasError: false });
                }, 2000)
            });
        });
    }

    moveItemDown = (item_id, e) => {
        axios.patch(`items/move-down`, {
            id: item_id,
        }).then(res => {
            this.getItems();
        }).catch(error => {
            this.setState({
                hasError: true,
                errorMessage: 'Failed to move item.'
            }, function () {
                setTimeout(() => {
                    this.setState({ hasError: false });
                }, 2000)
            });
        });
    }

    componentDidMount() {
        this.getItems();
    }

    render() {
        return <>
            <ToastNotif className={this.state.hasError ? undefined : 'hidden'} message={this.state.errorMessage} />

            { this.state.showCreateItemModal === true &&
                <CreateItemModal
                    item={this.state.item}
                    mode={this.state.createMode}
                    onCreate={this.handleItemCreated}
                    onCancel={this.hideCreateItemModal}
                />
            }

            <button onClick={(e) => this.showCreateItemModal('create', null, e)} className="bottom-3 right-3 fixed inline-flex hover:shadow-2xl items-center justify-center px-5 py-3 border border-transparent text-base font-medium rounded-full text-white bg-yellow-900 hover:bg-yellow-800 outline-none focus:outline-none">
                +
            </button>

            <div className="flex flex-col">
                <div className="-my-2 overflow-y-auto sm:-mx-6 lg:-mx-8">
                    <div className="py-2 align-middle inline-block min-w-full sm:px-6 lg:px-8">
                        <div className="shadow overflow-hidden border-b border-gray-200 sm:rounded-lg">
                            <table className="min-w-full divide-y divide-gray-200">
                                <tbody className="bg-white divide-y divide-gray-200">
                                    {this.state.items.map((item, index) => {
                                        return <>
                                            <tr className="flex py-4" key={item.id}>
                                                <td className="whitespace-nowrap flex-grow cursor-default">
                                                    <div className="flex items-center">
                                                        <div className="ml-4">
                                                            <div className="text-xs font-medium text-gray-900 text-left">
                                                                {item.name}
                                                    </div>
                                                            <div className="text-xs text-gray-500 text-left">
                                                                {item.price}
                                                    </div>
                                                        </div>
                                                    </div>
                                                </td>
                                                <td className="flex-shrink">
                                                    <div className="border-2 border-solid border-gray rounded-md inline-block p-1 mr-2">
                                                        <ArrowUp className="text-gray-700 cursor-pointer" 
                                                            onClick={(e) => this.moveItemUp(item.id, e)} 
                                                        />
                                                    </div>
                                                    <div className="border-2 border-solid border-gray rounded-md inline-block p-1 mr-2">
                                                        <ArrowDown className="text-gray-700 cursor-pointer" 
                                                            onClick={(e) => this.moveItemDown(item.id, e)} 
                                                        />
                                                    </div>
                                                    <div className="border-2 border-solid border-gray rounded-md inline-block p-1 mr-2">
                                                        <Pencil 
                                                            className="text-gray-700 cursor-pointer" 
                                                            onClick={(e) => this.showCreateItemModal('update', item, e)} 
                                                        />
                                                    </div>
                                                </td>
                                            </tr>
                                        </>
                                    })}
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
            </div>
        </>;
    }

}

export default MenuItemList;