import React from 'react'
import axios from 'axios';
import formatMoney from '../Helpers';
import ToastNotif from './ToastNotif';

class Reports extends React.Component {

    state = {
        datefrom: null,
        dateto: null,
        order_count: 0,
        total_revenue: 0,
        average_revenue_per_order: 0,
        items: [],
        hasError: false,
        errorMessage: ''
    }

    getReports = () => {
        axios.get(`reports?datefrom=${this.state.datefrom}&dateto=${this.state.dateto}`)
            .then(res => {
                const report = res.data;
                this.setState({
                    order_count: report.order_count,
                    total_revenue: report.total_revenue,
                    average_revenue_per_order: report.average_revenue_per_order,
                    items: report.items
                });
            }).catch(error => {
                this.setState({
                    hasError: true,
                    errorMessage: 'Failed to get reports.'
                }, function () {
                    setTimeout(() => {
                        this.setState({ hasError: false });
                    }, 2000)
                });
            });;
    }

    formatDate = (date = null) => {
        if (date) {
            let d = new Date(date),
                month = '' + (d.getMonth() + 1),
                day = '' + d.getDate(),
                year = d.getFullYear();

            if (month.length < 2)
                month = '0' + month;
            if (day.length < 2)
                day = '0' + day;

            return [month, day, year].join('-');
        } else {
            let d = new Date(),
                month = '' + (d.getMonth() + 1),
                day = '' + d.getDate(),
                year = d.getFullYear();

            if (month.length < 2)
                month = '0' + month;
            if (day.length < 2)
                day = '0' + day;

            return [year, month, day].join('-');
        }


    }

    updateDateFrom = (e) => {
        this.setState({ datefrom: e.target.value }, this.getReports);
    }

    updateDateTo = (e) => {
        this.setState({ dateto: e.target.value }, this.getReports);
    }

    componentDidMount() {
        let today = this.formatDate();
        this.setState({
            datefrom: today,
            dateto: today
        }, () => {
            this.getReports();
        });
    }

    render() {
        return <>
            <ToastNotif className={this.state.hasError ? undefined : 'hidden'} message={this.state.errorMessage} />

            <div className="text-left p-6">
                <div className="pb-6">
                    <label htmlFor="datefrom" className="block text-sm font-medium text-gray-700">Date From</label>
                    <input 
                        type="date" id="datefrom" 
                        className="mt-1 focus:ring-yellow-900 focus:border-yellow-500 block w-full shadow-sm sm:text-sm border-gray-900 rounded-md" 
                        defaultValue={this.state.datefrom} 
                        onChange={this.updateDateFrom} 
                    />
                </div>

                <div className="pb-6">
                    <label htmlFor="dateto" className="block text-sm font-medium text-gray-700">Date To</label>
                    <input 
                        type="date" id="dateto" 
                        className="mt-1 focus:ring-yellow-900 focus:border-yellow-500 block w-full shadow-sm sm:text-sm border-gray-900 rounded-md" 
                        min={this.state.datefrom} 
                        defaultValue={this.state.dateto} 
                        onChange={this.updateDateTo} 
                    />
                </div>

                <span className="cursor-default">

                <div className="pb-6">
                    Number of orders: { this.state.order_count }
                </div>

                <div className="pb-6">
                    Total revenue: { formatMoney(parseFloat(this.state.total_revenue)) }
                </div>

                <div className="pb-6">
                    Average revenue per order: { formatMoney(parseFloat(this.state.average_revenue_per_order)) }
                </div>

                <div className="pb-6">
                    Order item breakdown:
                    <table className="ml-2">
                        <tbody>
                            {this.state.items.map((item, index) => {
                                return <tr>
                                    <td className="px-2">{item.name}</td>
                                    <td className="px-2">{item.c}</td>
                                </tr>;
                            })}
                        </tbody>
                    </table>
                </div>

                </span>


            </div>
        </>;
    }
}

export default Reports;