import React from 'react'
import OrderList from './OrderList';
import MenuItemList from './MenuItemList';
import ToastNotif from './ToastNotif';
import CompletedOrderList from './CompletedOrderList';
import Reports from './Reports';
import { Menu, ClipboardList, ClipboardCheck, DocumentReport } from "heroicons-react";
import icon from '../icon.jpg';

class POSApp extends React.Component {
    state = {
        isSideNavopen: false,
        activePage: 'pending-orders'
    }

    appheight = {
        height: 'calc(100vh - 4rem)',
        fallbacks: [
            { height: '-moz-calc(100vh - 4rem)' },
            { height: '-webkit-calc(100vh - 4rem)' },
            { height: '-o-calc(100vh - 4rem)' }
        ]
    }

    appwidth = {
        width: 'calc(100vw - 16rem)'
    }

    changeActivePage = (page) => {
        this.setState({ activePage: page });
        this.hideSideNav();
    }

    hideSideNav = () => {
        this.setState({ isSideNavopen: false });
    }

    toggleSideNav = () => {
        this.setState({ isSideNavopen: !this.state.isSideNavopen });
    }

    render() {
        return <>
            {/* <ToastNotif message="hahahaha" /> */}

            <aside className={`transform top-0 left-0 fixed w-64 h-full ease-in-out transition-all duration-300 z-30 px-6 py-3 border-r-2 border-solid border-gray-300 border-opacity-50 lg:translate-x-0 ${this.state.isSideNavopen ? 'translate-x-0' : '-translate-x-full'}`}>
                <div className="cursor-default px-6 h-12 mt-1 text-lg border-b-2 border-solid border-gray-300 ">
                    Cupp'A Joe
                </div>

                <span className="-m-2 py-6 mt-2 flex items-center rounded-md cursor-pointer" onClick={(e) => this.changeActivePage('pending-orders', e)}>
                    <ClipboardList />
                    <span className="ml-3 text-base font-medium text-gray-900">
                        Pending Orders
                    </span>
                </span>
                <span className="-m-2 py-6 flex items-center rounded-md cursor-pointer" onClick={(e) => this.changeActivePage('completed-orders', e)}>
                    <ClipboardCheck />
                    <span className="ml-3 text-base font-medium text-gray-900">
                        Completed Orders
                    </span>
                </span>
                <span className="-m-2 py-6 flex items-center rounded-md cursor-pointer" onClick={(e) => this.changeActivePage('menu-items', e)}>
                    <img className="h-8 w-7" src={icon} alt="" />
                    <span className="ml-3 text-base font-medium text-gray-900">
                        Menu Items
                    </span>
                </span>
                <span className="-m-2 py-6 flex items-center rounded-md cursor-pointer" onClick={(e) => this.changeActivePage('reports', e)}>
                    <DocumentReport />
                    <span className="ml-3 text-base font-medium text-gray-900">
                        Reports
                    </span>
                </span>
            </aside>

            <div className={`transform fixed h-full w-screen ease-in-out transition-all duration-300 lg:translate-x-64 ${this.state.isSideNavopen ? 'translate-x-64' : 'translate-x-0'}`} onClick={this.state.isSideNavopen ? this.hideSideNav : undefined}  style={window.innerWidth >= 1024? this.appwidth: undefined}>
                
                {this.state.isSideNavopen ? <div className="transform top-0 left-0 fixed w-screen h-full z-30 bg-gray-500 bg-opacity-75">
                </div> : undefined}
                
                <nav
                    className="flex fixed w-full items-center justify-between px-6 h-16 bg-white text-gray-700 border-b border-gray-200 z-10"
                >
                    <Menu className="lg:hidden" onClick={this.state.isSideNavopen ? undefined : this.toggleSideNav} />
                </nav>

                <div className="mt-16 overflow-y-auto" style={this.appheight}>
                    {this.state.activePage === 'pending-orders'? <OrderList />: undefined}
                    {this.state.activePage === 'completed-orders'? <CompletedOrderList />: undefined}
                    {this.state.activePage === 'menu-items'? <MenuItemList />: undefined}
                    {this.state.activePage === 'reports'? <Reports />: undefined}
                </div>

            </div>
        </>;
    }

}

export default POSApp;